body {
  padding: 75px 20px 20px 20px;
}

#acm {
  position: fixed;
}

.chart {
  min-height: 350px;
}

.code-longbox {
  overflow: auto;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  font-family: monospace;
  word-wrap: break-word;
}

.row-expanded div {
  max-height: 500px;
}

.row-normal div {
  max-height: 50px;
}

.row-highlight {
  font-weight: bold;
}

.suggestions-panel-parent {
  position: inherit;
}

.suggestions-panel {
  border-radius: 0 !important;
  -webkit-box-shadow: 10px 0 50px #888888 !important;
          box-shadow: 10px 0 50px #888888 !important;
  position: absolute !important;
  max-height: 500px;
  overflow: auto;
  top: 45px;
  width: 100%;
  cursor: pointer;
}

a.navbar-brand {
  padding: 8px;
}

.navbar-default {
  background-color: #49525a !important;
}

.legend-label {
  font-size: 10px;
  width: 100px;
  padding: 5px;
  margin: 0;
}

.legend-label:hover {
  cursor: pointer;
}

.navbar-fixed-bottom-custom {
  background-color: #ecf0f1;
}

.navbar-search-border-right-input {
  border-bottom-right-radius: 4px !important;
  border-top-right-radius: 4px !important;
}

/* C3 customizations. */

#main-container .c3-grid line {
  stroke: #ecf0f1;
}

#main-container .c3-xgrid,
#main-container .c3-ygrid {
  stroke-dasharray: none;
}

#main-container .c3-tooltip td {
  border-style: none;
}

/* UI legibility improvements. */

input#searchBox {
  border-color: transparent;
  width: 100%;
}

/* Sort icons */

.call-tracer-sort-inactive {
  color: #c9d5d6;
  font-size: 10px;
}

.call-tracer-sort-active {
  color: #95a5a6;
  font-size: 10px;
}

.clickable {
  cursor: pointer;
}

.panel-subtitle {
  font-size: 13px;
}

.calles-nav {
  float: right;
}

.pagination-no-margin {
  margin-top: 0px;
  margin-bottom: 0px;
}

.pagination > li > a {
  padding: 11px 15px;
}

.graph-util-button {
  margin-left: 4px;
}

.graph-utils-callee {
  margin-left: 8px;
  margin-right: 2px;
  margin-top: 2px;
  font-size: 13px;
}

.graph-utils-fold {
  margin-top: -2px;
  margin-right: -2px;
  -webkit-text-stroke: 2.8px #ecf0f1;
}

.graph-utils-close {
  margin-top: -5px;
  font-size: 26px;
}

.graph-utils-fav {
  margin-top: 2px;
  font-size: 15px;
  -webkit-text-stroke: 1.5px;
}

.glyphicon-thick::before {
  font-weight: 900;
}

.callees-panel-parent {
  position: relative;
}

.callees-panel {
  border-radius: 0 !important;
  -webkit-box-shadow: 10px 0 50px #888888 !important;
          box-shadow: 10px 0 50px #888888 !important;
  position: absolute !important;
  max-height: 500px;
  overflow: auto;
  top: -22px;
  right: 42px;
  min-width: 300px;
  cursor: default;
  z-index: 999999;
}

.callee-item {
  cursor: pointer;
  margin-right: 20px;
}

.callee-item:hover {
  font-weight: bold;
}

.function-separator {
  margin-top: 30px;
  margin-bottom: 30px;
  border-top: 2px solid #fe6b2740;
}

.notification-enter {
  opacity: 0.01 !important;
}

.notification-enter-active {
  opacity: 0.7 !important;
  -webkit-transition: all 400ms ease-out !important;
  -o-transition: all 400ms ease-out !important;
  transition: all 400ms ease-out !important;
}

.notification-exit-active {
  opacity: 0.01 !important;
  -webkit-transition: all 200ms ease-out !important;
  -o-transition: all 200ms ease-out !important;
  transition: all 200ms ease-out !important;
}

.disabled-prev-next {
  background-color: #67d8b7 !important;
}

/* Grid switch icon */
.btn-grid-switch {
  padding: 2px 7px;
}

.glyphicon-grid-switch {
  font-size: 32px;
  left: 2px;
}

.navbar-right-more-margin {
  margin-right: -5px;
}

.btn-input-switch:hover {
  border-color: #95a5a6;
}

.btn-active-disabled {
  color: #ffffff;
  background-color: #798d8f;
  border-color: #74898a;
}

.btn-active-disabled:disabled {
  cursor: inherit;
  opacity: 1;
  outline: 0;
  background-image: none;
  -webkit-box-shadow: inset -2px 1px 8px rgba(0, 0, 0, 0.125);
  box-shadow: inset -2px 1px 8px rgba(0, 0, 0, 0.125);
}

.btn-active-disabled:disabled:hover {
  opacity: 1;
  color: #ffffff;
  background-color: #798d8f;
  border-color: #74898a;
  z-index: 0;
}



